function updateUrl(param) {

var id = "";

  data.filter(function (i,n){

          //console.log(i.link,n);

          if (i.link == param) {
            //console.log(i.link);
            //console.log('item is: ' + n);
            id = n;
          }

      });


  return "?id="+id;
}

